{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color:goldenrod;
  text-decoration: none;
}
.container {
  padding: 0 1rem;
}
.btn {
  display: inline-block;
  border: none;
  background: #555;
  padding: 7px 20px;
  color: white;
  cursor: cell;
}

.btn:hover {
  background:  #666666;
}